.van-icon--image {
  width: 1em;
  height: 1em;
}
.van-icon--image img {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  max-width: 100%;
  max-height: 100%;
  position: absolute;
}
.van-loading {
  width: 30px;
  height: 30px;
  z-index: 0;
  font-size: 0;
  line-height: 0;
  position: relative;
  vertical-align: middle;
}
.van-loading--circle {
  width: 16px;
  height: 16px;
}
.van-loading__spinner {
  z-index: -1;
  width: 100%;
  height: 100%;
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  -webkit-animation: van-rotate 0.8s linear infinite;
  animation: van-rotate 0.8s linear infinite;
}
.van-loading__spinner--circle {
  border-radius: 100%;
  border: 3px solid transparent;
  border-color: #7d7e80 #c8c9cc #c8c9cc;
}
.van-loading__spinner--gradient-circle {
  background-size: contain;
  background-image: url(https://img.yzcdn.cn/vant/gradient-circle-black.png);
}
.van-loading__spinner--spinner {
  -webkit-animation-timing-function: steps(12);
  animation-timing-function: steps(12);
}
.van-loading__spinner--spinner i {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
}
.van-loading__spinner--spinner i::before {
  width: 2px;
  height: 25%;
  content: ' ';
  display: block;
  margin: 0 auto;
  border-radius: 40%;
  background-color: currentColor;
}
.van-loading__spinner--circular {
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
}
.van-loading__circular {
  width: 100%;
  height: 100%;
}
.van-loading__circular circle {
  stroke: currentColor;
  stroke-width: 3;
  stroke-linecap: round;
  -webkit-animation: van-circular 1.5s ease-in-out infinite;
  animation: van-circular 1.5s ease-in-out infinite;
}
.van-loading--white .van-loading__spinner--circle {
  border-color: rgba(255, 255, 255, 0.7) rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1);
}
.van-loading--white .van-loading__spinner--gradient-circle {
  background-image: url(https://img.yzcdn.cn/vant/gradient-circle-white.png);
}
.van-loading__spinner--spinner i:nth-of-type(1) {
  opacity: 1;
  -webkit-transform: rotate(30deg);
  transform: rotate(30deg);
}
.van-loading__spinner--spinner i:nth-of-type(2) {
  opacity: 0.9375;
  -webkit-transform: rotate(60deg);
  transform: rotate(60deg);
}
.van-loading__spinner--spinner i:nth-of-type(3) {
  opacity: 0.875;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
.van-loading__spinner--spinner i:nth-of-type(4) {
  opacity: 0.8125;
  -webkit-transform: rotate(120deg);
  transform: rotate(120deg);
}
.van-loading__spinner--spinner i:nth-of-type(5) {
  opacity: 0.75;
  -webkit-transform: rotate(150deg);
  transform: rotate(150deg);
}
.van-loading__spinner--spinner i:nth-of-type(6) {
  opacity: 0.6875;
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}
.van-loading__spinner--spinner i:nth-of-type(7) {
  opacity: 0.625;
  -webkit-transform: rotate(210deg);
  transform: rotate(210deg);
}
.van-loading__spinner--spinner i:nth-of-type(8) {
  opacity: 0.5625;
  -webkit-transform: rotate(240deg);
  transform: rotate(240deg);
}
.van-loading__spinner--spinner i:nth-of-type(9) {
  opacity: 0.5;
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}
.van-loading__spinner--spinner i:nth-of-type(10) {
  opacity: 0.4375;
  -webkit-transform: rotate(300deg);
  transform: rotate(300deg);
}
.van-loading__spinner--spinner i:nth-of-type(11) {
  opacity: 0.375;
  -webkit-transform: rotate(330deg);
  transform: rotate(330deg);
}
.van-loading__spinner--spinner i:nth-of-type(12) {
  opacity: 0.3125;
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg);
}
.van-button {
  display: inline-block;
  position: relative;
  padding: 0;
  height: 44px;
  line-height: 42px;
  font-size: 16px;
  border-radius: 2px;
  text-align: center;
  box-sizing: border-box;
  -webkit-appearance: none;
  -webkit-text-size-adjust: 100%;
}
.van-button::before {
  content: " ";
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0;
  width: 100%;
  height: 100%;
  border: inherit;
  border-color: #000;
  background-color: #000;
  border-radius: inherit;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.van-button:active::before {
  opacity: 0.1;
}
.van-button--disabled::before,
.van-button--loading::before {
  display: none;
}
.van-button--default {
  color: #323233;
  background-color: #fff;
  border: 1px solid #ebedf0;
}
.van-button--primary {
  color: #fff;
  background-color: #07c160;
  border: 1px solid #07c160;
}
.van-button--info {
  color: #fff;
  background-color: #1989fa;
  border: 1px solid #1989fa;
}
.van-button--danger {
  color: #fff;
  background-color: #f44;
  border: 1px solid #f44;
}
.van-button--warning {
  color: #fff;
  background-color: #ff976a;
  border: 1px solid #ff976a;
}
.van-button--plain {
  background-color: #fff;
}
.van-button--plain.van-button--primary {
  color: #07c160;
}
.van-button--plain.van-button--info {
  color: #1989fa;
}
.van-button--plain.van-button--danger {
  color: #f44;
}
.van-button--plain.van-button--warning {
  color: #ff976a;
}
.van-button--large {
  width: 100%;
  height: 50px;
  line-height: 48px;
}
.van-button--normal {
  padding: 0 15px;
  font-size: 14px;
}
.van-button--small {
  padding: 0 8px;
  height: 30px;
  min-width: 60px;
  font-size: 12px;
  line-height: 28px;
}
.van-button--loading .van-loading,
.van-button--mini {
  display: inline-block;
}
.van-button--mini {
  height: 22px;
  min-width: 50px;
  font-size: 10px;
  line-height: 20px;
}
.van-button--mini + .van-button--mini {
  margin-left: 5px;
}
.van-button--block {
  width: 100%;
  display: block;
}
.van-button--bottom-action {
  border: 0;
  width: 100%;
  height: 50px;
  font-size: 16px;
  line-height: 50px;
  border-radius: 0;
  color: #fff;
  background-color: #ff976a;
}
.van-button--bottom-action.van-button--primary {
  background-color: #f44;
}
.van-button--disabled {
  opacity: 0.5;
}
.van-button--hairline.van-button--round::after,
.van-button--round {
  border-radius: 10em;
}
.van-button--hairline.van-button--square::after,
.van-button--square {
  border-radius: 0;
}
.van-button__loading-text {
  margin-left: 5px;
  display: inline-block;
  vertical-align: middle;
}
.van-button--hairline {
  border-width: 0;
}
.van-button--hairline::after {
  border-color: inherit;
  border-radius: 4px;
}
.van-cell {
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;
  color: #323233;
  font-size: 14px;
  line-height: 24px;
  background-color: #fff;
  padding: 10px 15px;
}
.van-cell:not(:last-child)::after {
  content: ' ';
  position: absolute;
  pointer-events: none;
  box-sizing: border-box;
  left: 15px;
  right: 0;
  bottom: 0;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
  border-bottom: 1px solid #ebedf0;
}
.van-cell--borderless::after {
  display: none;
}
.van-cell__label {
  color: #969799;
  font-size: 12px;
  margin-top: 3px;
  line-height: 18px;
}
.van-cell__title,
.van-cell__value {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
}
.van-cell__value {
  color: #969799;
  overflow: hidden;
  text-align: right;
  position: relative;
  vertical-align: middle;
}
.van-cell__value--alone {
  color: #323233;
  text-align: left;
}
.van-cell__left-icon,
.van-cell__right-icon {
  min-width: 1em;
  height: 24px;
  font-size: 16px;
  line-height: 24px;
}
.van-cell__left-icon {
  margin-right: 5px;
}
.van-cell__right-icon {
  margin-left: 5px;
  color: #969799;
}
.van-cell--clickable:active {
  background-color: #f2f3f5;
}
.van-cell--required {
  overflow: visible;
}
.van-cell--required::before {
  content: '*';
  position: absolute;
  left: 7px;
  font-size: 14px;
  color: #f44;
}
.van-cell--center {
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
}
.van-cell--large {
  padding-top: 12px;
  padding-bottom: 12px;
}
.van-cell--large .van-cell__title {
  font-size: 16px;
}
.van-cell--large .van-cell__label {
  font-size: 14px;
}
.van-cell-group {
  background-color: #fff;
}
.van-cell-group__title {
  color: #969799;
  padding: 15px 15px 5px;
  font-size: 14px;
  line-height: 16px;
}
.van-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}
